<template>
  <div>
    <mavon-editor :toolbars="markdownOption" v-model="htmlMD" />
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: [],
  data() {
    return {
      // markdown配置项
      markdownOption: {
        subfield: false,
        editable: false, // 不允许编辑
        toolbarsFlag: false, // 不显示工具栏
        defaultOpen: "preview", //展示预览
        navigation: true,
      },
      htmlMD: {},
    };
  },
  computed: {},
  watch: {},
  created() {},
  async mounted() {
    const { data } = await this.$get("jQuery-ajax.md");
    this.htmlMD = data;
  },
  methods: {},
};
</script>

